import * as React from "react";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import html2canvas from "html2canvas";
import "./home.css";
import { QRCode } from "react-qrcode-logo";
import { logo_base64 } from "../service/constants";
import styled from "styled-components";
import { Link, Slider } from "@mui/material";
interface HomePageProps {}

export const HomePage: React.FC<HomePageProps> = () => {
  const [errorText, setErrorText] = React.useState<string>("");
  const [mobile, setMobile] = React.useState<string>("");
  const [qrURL, setQrURL] = React.useState<string>("rescueqr");
  const [qrWidth, setQrWidth] = React.useState<any>(175);
  const [activeStep, setActiveStep] = React.useState(0);
  const [showHowTo, setShowHowTo] = React.useState(false);

  const generateOfflineQR = (event: any) => {
    event.preventDefault();
    setQrURL(event.target.mobile.value);
    setActiveStep(activeStep + 1);
  };

  const execQR = (value: any) => {
    if (value) {
      setQrURL(value);
      setActiveStep(activeStep + 1);
    }
  };

  const OnChangeHandler = (event: any) => {
    if (event && isNaN(event.target.value)) {
      setErrorText("Please enter only numbers!");
      event.preventDefault();
      setMobile("");
    } else {
      setErrorText("");
      setMobile(event.target.value);
      execQR(event.target.value);
    }
  };
  const handleDownload = () => {
    html2canvas(document.querySelector("#react-qrcode-logo") as any).then(
      function (canvas) {
        const link = document.createElement("a");
        link.download = "rescue-qr.png";
        link.href = canvas.toDataURL();
        link.click();
      }
    );
  };

  const handleQrSizeChange = (event: Event, newValue: number | number[]) => {
    console.log("handleQrSizeChange :: ", newValue);
    setQrWidth((newValue as number) + 50);
  };

  const howToUse = () => {
    setShowHowTo(!showHowTo);
  };

  return (
    <>
      <div className="container">
        {showHowTo && (
          <div className="howTo">
            <CustomHowToImage
              src={window.location.origin + "/images/howto.png"}
            />
          </div>
        )}

        <div className="homePanel">
          <div className="rescuePanel">
            <h1>Rescue QR</h1>
            <CustomBgLogo
              src={window.location.origin + "/images/logo/logo.png"}
            />
            <Button
              variant="text"
              sx={{ mt: 5, ml: 8 }}
              style={{ color: "white" }}
              onClick={howToUse}
            >
              How To Use Rescue QR
            </Button>
          </div>

          <div className="qrPanel">
            <TextField
              required
              error={errorText ? true : false}
              id="mobile"
              label="Mobile"
              name="mobile"
              style={{ marginLeft: "10%", marginTop: "10%" }}
              autoComplete="given-name"
              variant="standard"
              onChange={(e) => OnChangeHandler(e)}
              helperText={errorText}
            />
            <QRSlider
              size="small"
              defaultValue={125}
              aria-label="Small"
              valueLabelDisplay="auto"
              onChange={handleQrSizeChange}
              step={10}
              marks
              min={10}
              max={125}
            />
            <QRPanel>
              <QRCode
                size={qrWidth}
                style={{
                  height: "auto",
                  maxWidth: "100%",
                  width: "100%",
                  borderWidth: "1px",
                  borderColor: "green",
                  borderStyle: "dotted"
                }}
                removeQrCodeBehindLogo={false}
                qrStyle="dots"
                logoHeight={60}
                logoWidth={60}
                logoOpacity={0.3}
                value={qrURL}
                quietZone={27}
                logoImage={logo_base64}
              />
              <Button
                variant="outlined"
                sx={{ mt: 2, ml: 3 }}
                onClick={handleDownload}
                disabled={errorText ? true : false}
              >
                Download Rescue QR
              </Button>
            </QRPanel>
          </div>
        </div>
      </div>
    </>
  );
};

export default HomePage;

export const QRPanel = styled.div`
  margin-left: 12%;
`;

export const QRSlider = styled(Slider)`
  width: 80% !important;
  margin-left: 10%;
  height: 20px !important;
`;

export const CustomLogo = styled.img`
  width: 15%;
  float: left;
  margin-left: 5%;
`;

export const CustomBgLogo = styled.img`
  width: 75%;
  float: left;
  margin-left: 14%;
`;

export const CustomHowToImage = styled.img`
  width: 100%;
`;

export const CustomTitle = styled.h1`
  float: right;
`;

export const CustomLink = styled(Link)`
  margin-top: 30%;
`;
